import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)



const routes = [
  {
    path: '/',
    name: '',
    component: () => import('../views/QrCode.vue')
  },
  {
    path: '/admin',
    name: 'admin-page',
    component: () => import('../views/Admin.vue')
  }
]

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes
})

export default router
